import shuffle from 'lodash.shuffle';
import ImageOfCards from './imgCards';

const numberOfCards = 8;

export default () => {
  // console.log(imageOfCards);
  const deckOfCards = ImageOfCards();
  const cards = [];
  // repartir aleatoriamente
  while (cards.length < numberOfCards) { // cuando tenga el nmero de cartas deja de mandar
    // crear un indice aleatorio
    const index = Math.floor(Math.random() * deckOfCards.length);
    // multiplicamos por el numero de cartas de que tenemos
    // Math.floor redondea hacia abajo

    // extraer los elementos que estn en el deckOfCards
    const card = {
      image: deckOfCards.splice(index, 1)[0],
      // splice para extraer la carta que queremos del indice aleatoriamente
      // nos devuelve solo un elemento, por eso escogemos el elemento que est en la posicin 0
      guessed: false,
    };
    cards.push(card); // guardamos el objeto en un array
    cards.push({ ...card }); // la clonamos con las mismas propiedades pero no la misma referencia
  }

  return shuffle(cards);
  // como ambas estn en posiciones consecutivas
  // usaremos el modulo lodash suffle para mezclar las cartas
};



